"use client";

import React, { useEffect, useRef, useState } from 'react';
import { createPortal } from 'react-dom';

export default function BackgroundPortal({ children }: { children: React.ReactNode }) {
    const [mounted, setMounted] = useState(false);
    const hostRef = useRef<HTMLElement | null>(null);

    useEffect(() => {
        const host = document.createElement('div');
        host.setAttribute('id', 'app-bg-portal');
        // cover viewport and sit behind everything
        host.style.position = 'fixed';
        host.style.inset = '0';
        host.style.zIndex = '-99999';
        host.style.pointerEvents = 'none';
        document.body.appendChild(host);
        hostRef.current = host;
        setMounted(true);
        return () => {
            if (hostRef.current) document.body.removeChild(hostRef.current);
        };
    }, []);

    if (!mounted || !hostRef.current) return null;
    return createPortal(children, hostRef.current);
}
